  <template>
  <v-app>
    <v-toolbar color="indigo" dark fixed app>
      <v-toolbar-title>Swivel</v-toolbar-title>
    </v-toolbar>
    <v-container  grid-list-md text-xs-center>
     
      <v-layout align-center justify-center>
 <v-toolbar card prominent >  
      <v-toolbar-title>Select Table</v-toolbar-title>
      <v-toolbar-title>
        <v-select
            v-bind:items="items"
            @change="onChange($event)"
            label="Select Table"
            item-value="item"
            item-text="name"
            v-model="selected"
          ></v-select>
      </v-toolbar-title>
      
      <v-toolbar-title>
        <v-btn @click="toggle = !toggle" id="previousBtn" color="success">next</v-btn>
        </v-toolbar-title>
      
    </v-toolbar>
      </v-layout>
      <div v-show="toggle">
        <v-container fluid grid-list-md>
          <v-layout row wrap>
            <v-flex d-flex xs12 sm6 md6>
              <v-card color>
                <v-card-title primary class="title">Available Field</v-card-title>
                <v-card-text>
                  <v-flex xs12>
                    <table
                      id="example"
                      class="table table-hover table-bordered dt-responsive"
                      cellspacing="0"
                      width="100%"
                    >
                      <tr>
                        <th>Select</th>
                        <th>Field</th>
                      </tr>

                      <tbody>
                        <tr v-for="question in tableData" :key="question.field">
                          <td>
                            <v-checkbox :value="question.field" v-model="chkselect"></v-checkbox>
                            
                          </td>
                          <td>{{question.field}} ({{question.display}})
                            <input type="hidden" v-model="question.display">
                          </td>
                        </tr>


                      </tbody>
                    </table>
                  </v-flex>
                </v-card-text>
              </v-card>
            </v-flex>
             
            <v-flex d-flex xs12 sm6 md6 child-flex>
              <v-card color>
                <v-card-title primary class="title">Report Field</v-card-title>
                <v-card-text >
                   
                   <table
                    id="example"
                    class="table table-hover table-bordered dt-responsive"
                    cellspacing="0"
                    width="100%"
                  >
                    <tr>
                      <th>Field</th>
                      <th>Display Name</th>
                    </tr>

                    <!-- <tbody > -->
                        <draggable :list="chkselect" :element="'tbody'">
                     <tr v-for="items in chkselect" :key="items">
                        <td>{{items}}</td>
                        <td>
                          <input type="text" class="form-control" v-bind:value="items">
                        </td>
                      </tr>
                </draggable>
                    <!-- </tbody> -->
                    
                  </table> 
                </v-card-text> 
              </v-card>
            </v-flex>
             
          </v-layout>
        </v-container>
      </div>
      <v-layout wrap row>
        <v-flex xs12 sm6></v-flex>
        <v-flex xs12 sm6></v-flex>
      </v-layout>
    </v-container>

    <v-footer color="indigo" app>
      <span class="white--text">&copy; 2019</span>
    </v-footer>
  </v-app>
</template>


<script src="https://unpkg.com/vue@2.5.3/dist/vue.js"></script>
<script>

import HelloWorld from "./components/HelloWorld";
import VueDataTable from "./components/VueDataTable";
import axios from "axios";
import draggable from 'vuedraggable'

export default {
  name: "App",
  components: {
    HelloWorld,
    VueDataTable,
    draggable
  },
  data() {
    return {
      tableBooking: [],
      results: [],
      columns: ["field", "display", "type", "length"],
      tableData: [],
      chkselect:[],
      options: {
        headings: {
          field: "field",
          display: "display",
          type: "type",
          length: "length"
        },
        sortable: ["field", "display", "type", "length"]
      },
      items: [
        { name: "Booking", value: "booking" },
        { name: "Stock In", value: "stockin" },
        { name: "Stock Out", value: "stockout" }
      ],
      selected: null,
      toggle:false,
      
    };
  },
  methods: {
onChange(event) {
      if (this.selected == "Booking") {
        this.chkselect=[];
        this.axios.get("http://localhost:5555/booking").then(response => {
          this.tableData = response.data;
        });
      } 
      else if (this.selected == "Stock In") {
        this.chkselect=[];
      document.getElementById("previousBtn").disabled = true;
      this.axios.get("http://localhost:5555/stockin").then(res => {
          this.tableData = res.data;
        });
      } 
      else 
      {
        this.chkselect=[];
        document.getElementById("previousBtn").disabled = true;
        this.axios.get("http://localhost:5555/stockout").then(res => {
          this.tableData = res.data;

        });
      }
    }
  }
};
</script>
